import { Injectable } from '@angular/core';
import { RxStompConfig, RxStomp } from '@stomp/rx-stomp';
import { environment } from 'src/environments/environment';

//Configuration
export const myRxStompConfig: RxStompConfig = {
  // server
  brokerURL: environment.backend_ws,

  // Headers
  // Typical keys: login, passcode, host
  connectHeaders: {
    login: '',
    passcode: '',
  },

  // How often to heartbeat (milliseconds) - 0 to disable
  heartbeatIncoming: 0, // Typical value 0 - disabled
  heartbeatOutgoing: 20000, // Typical value 20000 - every 20 seconds

  // Wait in milliseconds before attempting auto reconnect
  // Set to 0 to disable
  // Typical value 500 (500 milli seconds)
  reconnectDelay: 30*1000,

  // Will log diagnostics on console
  // It can be quite verbose, not recommended in production
  // Skip this key to stop logging to console
  debug: (msg: string): void => {
    console.log(new Date(), msg);
  },
};

@Injectable({
  providedIn: 'root'
})
export class RxStompService extends RxStomp {

  constructor() { super(); }
}

//singleton
const rxStomp:RxStompService = new RxStompService();

export function rxStompServiceFactory() {
  //const rxStomp = new RxStompService();
  rxStomp.configure(myRxStompConfig);
  rxStomp.activate();
  return rxStomp;
}

export function rxStompServiceDestroy(){
  rxStomp.deactivate();
}
