import { Component, Input, OnChanges, SimpleChanges, EventEmitter, Output } from '@angular/core';
import { Sensor } from '../../interfaces/sensor';
import { NgbModal, NgbModalConfig } from '@ng-bootstrap/ng-bootstrap';
import { Device } from '../../interfaces/device';

@Component({
  selector: 'app-bed-canvas',
  templateUrl: './bed-canvas.component.html',
  styleUrls: ['./bed-canvas.component.css'],
})
export class BedCanvasComponent implements OnChanges {

  
  /**
   * Path of the picture to show
   */
  @Input() picture_path: string = "assets/images/mbox_sleep.png";
  /**
   * Configures how the launcher will be rendered
   * possible values: picture (default) | button
   */
  @Input() mode:string = "picture";
  /**
   * Device object with all the elements describing the device 
   */
  @Input() device!:Device | null;
  @Input() sensorList!: Sensor[];

  @Output() submitObject: EventEmitter<Sensor> = new EventEmitter();

  width:number =110;
  height:number =190;//mm = (pixels * 25.4) / dpi

  bedStyle:any = {"width":"110mm", "height":"190mm","min-width": "110mm", "min-height":"190mm","z-index":2};
  viewBoxStyle:any = {"width":"110mm", "height":"190mm","min-width": "110mm", "min-height":"190mm"};
  renderReady:boolean = false;

  currentEditable!:Sensor;
  updateEvent:boolean = false;
  

  constructor(private modalService: NgbModal){
    //config.
  }
  
  ngOnChanges(changes: SimpleChanges) {
    // changes.prop contains the old and the new value...
    let device = changes['device'];
    let list = changes['sensorList'];
    if(device){
      this.width = device.currentValue.xBedDimension;
      this.height = device.currentValue.yBedDimension;
    }else
    if(list){
      console.log("render ready");
      console.log(this.sensorList);
      this.renderReady = true; 
    }
  }

  print(value:any){
    console.log("print");
    console.log(value);

  }

  showModal(content:any){
    this.modalService.open(content, {  modalDialogClass: 'bedmap-modal', scrollable: true, centered: true, size:'lg' });
  }

  getFilteredSensorList(sensorList:Sensor[],type:string):Sensor[]{
    const fsrList = sensorList.filter(
      (sensor) => sensor.transducerType.toUpperCase() == type.toUpperCase()
    );
    return fsrList;
  }

  getStringBedDimensions():string{
    const x = this.device? this.device.xBedDimension: this.width.toString();
    const y = this.device? this.device.yBedDimension: this.height.toString();
    const unit = this.device? this.device.bedDimensionUnits: "cm";
    let label = "" + x +" x "+ y +" "+unit;
    
    return label;
  }

  /**
   * calculate the proportion of pixels to maintain a scale in the bed picture
   * using the formula:
   * mm = (pixels * 25.4) / dpi
   * @param value the value to convert 
   * @param add  value to add
   * @param substract value to substract
   * @returns 
   */
  measureToPixels(value:number, add=0, substract=0):number{
    const dpi = window.devicePixelRatio*96;
    //console.log("dpi");
    //console.log(dpi);
    const pixel = ((value*dpi)/25.4) + add - substract;
    //console.log("pixel");
    //console.log(pixel);
    return pixel;
  }

  magnifyMagnitude(value:any,times:number): number{
    let result = value as number;
    result = result*times;
    
    return result;
  }

  
  setEditable(sensor:Sensor){
    this.currentEditable = sensor;
  }

  sendActiveSensor = (sensor:Sensor)=>{
    console.log("click!")
    console.log(sensor);
  };
  sendDeactiveSensor = (sensor:Sensor)=>{
    console.log("click!")
    console.log(sensor);
  };

  getBedDeviceStyle(): any{
    if(this.device){
      const width = this.device.xBedDimension?.toString().concat("mm");
      const height = this.device.yBedDimension?.toString().concat("mm");
      
      const style ={ "width":width, "height":height, "min-width": width, "min-height": height};
      //console.log("applying style");
      //console.log(style);
      return style;

    }
    console.log("default style");
    return this.bedStyle;
  }

  getViewBoxValues(): any{
    if(this.device){
      var width = this.device.xBedDimension? Number(this.device.xBedDimension): 110;
      var height = this.device.yBedDimension? Number(this.device.yBedDimension): 190;
      
      //apply pixelation
      width = this.measureToPixels(width);
      height = this.measureToPixels(height);

      const viewbox = "0,0,".concat(width.toString()).concat(",").concat(height.toString());
      console.log("applying viewbox");
      console.log(viewbox);
      return viewbox;

    }
    console.log("default style");
    return this.viewBoxStyle;
  }

}
