import { Component, EventEmitter, Input, OnChanges, OnInit, Output, SimpleChanges } from '@angular/core';
import {FormBuilder, FormControl, FormGroup} from '@angular/forms';
import { NgbModalConfig, NgbModal, NgbModalRef } from '@ng-bootstrap/ng-bootstrap';
import { GlobalUtilService } from '../../services/global-util.service';
import { Sensor } from '../../interfaces/sensor';
import { RequestService } from '../../services/request.service';
import { BackendmapService } from '../../services/backendmap.service';
import { AgentMessage } from '../../interfaces/agent-message';
import { SessionService } from '../../services/session.service';
import { CustomSensor } from '../../data/custom-sensor';
import { FSRSensor } from '../../data/fsrsensor';
import { IMUSensor } from '../../data/imusensor';
import { PtzSensor } from '../../data/ptz-sensor';
import { AccSensor } from '../../data/acc-sensor';
import { Observable, map, startWith } from 'rxjs';

@Component({
  selector: 'app-link-sensor-form',
  templateUrl: './link-sensor-form.component.html',
  styleUrls: ['./link-sensor-form.component.css']
})
export class LinkSensorFormComponent implements OnChanges, OnInit {

  @Input('mode') mode: string ="fab";
  @Input('title') title: string = "Edit Sensor Information";
  @Input('sensor') sensor!: Sensor;
  @Input('success') success!: boolean;
  @Input('error') error!: boolean;

  @Output() submitInfo: EventEmitter<string> = new EventEmitter();
  @Output() submitObject: EventEmitter<Sensor> = new EventEmitter();

  warnMessage: string = "Warning";
  errorEvent:boolean = false;
  isNewSensor:boolean = false;
  
  modalReference!:NgbModalRef;
  sensorForm!:FormGroup;
  templateFC:FormControl = new FormControl();

  DATA_TYPES:string[] = ['FSR','IMU'];
  suportedTypes!:Observable<string[]>;
  typesFC:FormControl = new FormControl();

  constructor(config: NgbModalConfig, private modalService: NgbModal, 
        private _request: RequestService, private _backend:BackendmapService,
        private _session: SessionService, public _util:GlobalUtilService) {

          // customize default values of modals used by this component tree
          config.backdrop = 'static';
          config.keyboard = false;
          
  }

  ngOnInit(): void {
    //init the form
    this.initSensorForm();
    //set listener to the template chooser
    this.templateFC.valueChanges.subscribe(
      selection =>{
        if(selection === "CUSTOM"){
          const template = new CustomSensor();
          this.resetForm(template);
          this.typesFC.patchValue(template.transducerType);
        }else
        if(selection === "FSR"){
          const template = new FSRSensor();
          this.resetForm(template);
          this.typesFC.patchValue(template.transducerType);

        }else 
        if(selection === "ACC"){
          const template = new AccSensor();
          this.resetForm(template);
          this.typesFC.patchValue(template.transducerType);

        }else 
        if(selection === "PTZ"){
          const template = new PtzSensor();
          this.resetForm(template);
          this.typesFC.patchValue(template.transducerType);

        }else 
        if(selection === "IMU"){
          const template = new IMUSensor();
          this.resetForm(template);
          this.typesFC.patchValue(template.transducerType);

        }
      }
    );
    //set listener to the type selector
    this.suportedTypes = this.typesFC.valueChanges
          .pipe(
            startWith(''),
            map(value => this._filterTypes(value)));
    //reset the control
    const current = this.sensorForm.get("transducerType")?.getRawValue();
    this.typesFC.patchValue(current);
    this.sensorForm.setControl("transducerType",this.typesFC);
  }

  private _filterTypes(value:string){
    const filterValue = value?.toLowerCase();
    return this.DATA_TYPES.filter(type => type.toLowerCase().includes(filterValue));
  }

  ngOnChanges(changes: SimpleChanges) {
    // changes.prop contains the old and the new value...
    let SensorEvent = changes['sensor'];
    let confirmEvent = changes['success'];
    let error = changes['error'];
    if(SensorEvent){
      let current:Sensor = changes['sensor'].currentValue;
      if(current !== undefined){
        this.initSensorForm();
        this.isNewSensor=false;
      }
    }else 
    //after submit changes    
    if(confirmEvent || error){
      this.modalClose();  
    }
  }

  modalOpen(content: any) {
		this.modalReference = this.modalService.open(content, { fullscreen: true, scrollable: true });
	}

  modalClose(){
    this.sensorForm?.reset();
    this.initSensorForm();
    this.modalReference?.close()
    //this.modalService.dismissAll('finished');
  }

  initSensorForm(): void{
    if(this.sensor !== undefined){
      this.sensorForm = new FormGroup({        
        uuid: new FormControl({value: this.sensor.uuid, disabled: true}),
        name: new FormControl(this.sensor.name),
        transducerType: new FormControl(this.sensor.transducerType),
        physicalDimension: new FormControl(this.sensor.physicalDimension),
        physicalMin: new FormControl(this.sensor.physicalMin),
        physicalMax: new FormControl(this.sensor.physicalMax),
        digitalMin: new FormControl(this.sensor.digitalMin),
        digitalMax: new FormControl(this.sensor.digitalMax),
        xLocation: new FormControl(this.sensor.xLocation),
        yLocation: new FormControl(this.sensor.yLocation),
        yDimension: new FormControl(this.sensor.yDimension),
        xDimension: new FormControl(this.sensor.xDimension),
        active: new FormControl(this.sensor.active),
        accessProtocol: new FormControl(this.sensor.accessProtocol),
        portAddress: new FormControl(this.sensor.portAddress),
        /*recorder: new FormGroup({
          comand: new FormControl(this.sensor.recorder.command),
          exec_path: new FormControl(this.sensor.recorder.exec_path),
          description: new FormControl(this.sensor.recorder.description)
        }),/** */
      });

      this.typesFC.setValue(this.sensor.transducerType);

    }else{
      this.isNewSensor = true;
      this.sensorForm = new FormGroup({
        template: this.templateFC,
        uuid: new FormControl(''),
        name: new FormControl(''),
        transducerType: new FormControl(''),
        physicalDimension: new FormControl(''),
        physicalMin: new FormControl(),
        physicalMax: new FormControl(),
        digitalMin: new FormControl(),
        digitalMax: new FormControl(),
        xLocation: new FormControl(),
        yLocation: new FormControl(),
        yDimension: new FormControl(),
        xDimension: new FormControl(),
        active: new FormControl(),
        accessProtocol: new FormControl(''),
        portAddress: new FormControl(),
        recorder: new FormGroup({
          comand: new FormControl(''),
          exec_path: new FormControl(''),
          description: new FormControl('')
        }),
      });
      this.typesFC.setValue('');
    }
  }

  setType(event:any):void{
    console.log(event);
    //this.sensorForm.get("type")?.patchValue(event.data);
  }

  getCurrentSensorUUID():string{
    const uuid_value = this.sensorForm.get("uuid")?.getRawValue();
    return uuid_value;
  }

  resetForm(defaultSensor:any=this.sensor): void{
    this.sensorForm.markAsUntouched();
    const current_uuid= this.sensorForm.get("uuid")?.getRawValue();
    this.sensorForm = new FormGroup({        
      uuid: new FormControl({value: current_uuid, disabled: !this.isNewSensor}),
      name: new FormControl(defaultSensor.name),
      transducerType: new FormControl(defaultSensor.transducerType),
      physicalDimension: new FormControl(defaultSensor.physicalDimension),
      physicalMin: new FormControl(defaultSensor.physicalMin),
      physicalMax: new FormControl(defaultSensor.physicalMax),
      digitalMin: new FormControl(defaultSensor.digitalMin),
      digitalMax: new FormControl(defaultSensor.digitalMax),
      xLocation: new FormControl(defaultSensor.xLocation),
      yLocation: new FormControl(defaultSensor.yLocation),
      yDimension: new FormControl(defaultSensor.yDimension),
      xDimension: new FormControl(defaultSensor.xDimension),
      active: new FormControl(defaultSensor.active),
      accessProtocol: new FormControl(defaultSensor.accessProtocol),
      portAddress: new FormControl(defaultSensor.portAddress),
      /*recorder: new FormGroup({
        comand: new FormControl(this.sensor.recorder.command),
        exec_path: new FormControl(this.sensor.recorder.exec_path),
        description: new FormControl(this.sensor.recorder.description)
      }),/** */
    });/** */
  }

  actualSubmit(data:any){
    this.submitObject.next(data);
  }

  onSubmit(){
    if(!this.sensorForm.invalid){
      let data = this.sensorForm.getRawValue();
      this.actualSubmit(data);
    }
  }

  getRandomUUID(): void{
    this._request.sendBackendGet(this._backend.GET_RANDOM_UUID_URL).subscribe({
      next:(res) => {
        this.sensorForm.get('uuid')?.setValue(res);
      },
      error:(err) => {
        console.error("HTTP-Error requesting auto UUID: "+err);
      },
      complete:() => {
        this._session.setEndQuery();
      }
    });

  }

}
