import { Component, OnInit } from '@angular/core';
import { MatSnackBar } from '@angular/material/snack-bar';
import { Device } from 'src/app/shared/interfaces/device';
import { RxStompService, rxStompServiceFactory } from 'src/app/shared/services/rx-stomp.service';
import { SessionService } from 'src/app/shared/services/session.service';


@Component({
  selector: 'app-sensors-page',
  templateUrl: './sensors-page.component.html',
  styleUrls: ['./sensors-page.component.css'],
  providers: [{provide: RxStompService, useFactory: rxStompServiceFactory}]
})
export class SensorsPageComponent implements OnInit{

  private currentDevice:Device|null = null;
  activeDevice!:Device;
  activeSession:boolean=false;

  constructor(private _snackBar: MatSnackBar, public _session: SessionService){
    this.currentDevice = this._session.getSessionDeviceData();
    if(this.currentDevice !== null){
      this.activeDevice = this.activeDevice;
      this.activeSession = true;
    }
  }
  ngOnInit(): void {
    this.currentDevice = this._session.getSessionDeviceData();
    console.log(this.currentDevice);
    if(this.currentDevice !=null){
      this.activeDevice = this.currentDevice;
      console.log("active-set");
    }
  }

  openSnackBar(message: any) {
    console.log(message);
    this._snackBar.open(message, 'dismiss',{
      duration: (7 * 1000),
    });
  }

}
