import { AfterViewInit, Component, EventEmitter, Input, OnChanges, OnInit, Output, SimpleChanges, ViewChild } from '@angular/core';
import {MatPaginator} from '@angular/material/paginator';
import {MatTableDataSource} from '@angular/material/table';
import { Sensor } from '../../interfaces/sensor';
import { SessionService } from '../../services/session.service';
import { BackendmapService } from '../../services/backendmap.service';
import { RequestService } from 'src/app/shared/services/request.service';
import { AgentMessage } from '../../interfaces/agent-message';
import { MatSort } from '@angular/material/sort';
import { Subscription, interval } from 'rxjs';
import { MatSnackBar } from '@angular/material/snack-bar';
import { Device } from '../../interfaces/device';
import { RxStompService } from '../../services/rx-stomp.service';
import { Message } from '@stomp/stompjs';

@Component({
  selector: 'app-sensors-data-table',
  templateUrl: './sensors-data-table.component.html',
  styleUrls: ['./sensors-data-table.component.css']
})
export class SensorsDataTableComponent implements OnInit, OnChanges{

  @Input('device') device!: Device;
  @Output() submitInfo: EventEmitter<string> = new EventEmitter();


  SENSOR_DATA: Sensor[] = [];
  infoMessage: string|undefined;
  errorMessage: string|undefined;
  showError:boolean = false;
  datacount: number = 0;
  subscriber!: any;

  RECORD_FILES:string[] =[];

  brokerFail:boolean = false;
  stompSubscriber!:Subscription;
  queueSubscription!:Subscription;
  activeUpdateEvent:boolean = false;
  submitSuccess:boolean = false;

  displayedColumns: string[] = ['uuid', 'name', 'transducerType', 'active', 'accessProtocol','controls'];
  dataSource!: MatTableDataSource<Sensor>;

  @ViewChild(MatSort) sort!: MatSort;
  @ViewChild(MatPaginator) paginator!: MatPaginator;

  constructor(private _rxStomp: RxStompService, private _request: RequestService, public _session:SessionService, 
    private _snackBar: MatSnackBar, private _backend:BackendmapService){

  }

  ngOnInit(): void {
    this.stompSubscriber = this._rxStomp.connected$
      .subscribe(
        state =>{
          console.log('state');
          console.log(state);
          console.log(this.device);
          //assign flag
          this.brokerFail = false;
          const uuid = this.device?this.device.uuid:'';
          //create subscription
          this.queueSubscription = this._rxStomp
          .watch('/topic/'.concat(uuid))
          .subscribe((message: Message) => {
              console.log('message recieved:');
              console.log(message);
              try{
                const response = JSON.parse(message.body);
                const params = response.PARAMS;

                //ONLINE_SIGNAL
                if(response.ACTION == 'ONLINE_SIGNAL'){
                  if(this.device!=null){
                    this.device.isOnline = (params == uuid);
                  }
                }else
                //PROCEDURE_FAIL
                if(response.ACTION == 'PROCEDURE_FAIL'){
                  this.errorMessage = params.origin.concat(' Error!: ').concat(params.message);
                  this.showError = true;
                  this.submitSuccess = false;
                  this.activeUpdateEvent = false;
                  this._session.setEndQuery();
                }else

                //LIST_SENSORS
                if(response.ACTION == 'LIST_SENSORS'){
                  console.log('LIST_SENSORS');
                  console.log(params);
                  if(this.device!=null){
                    this.SENSOR_DATA = params;
                    this.dataSource = new MatTableDataSource<Sensor>(this.SENSOR_DATA);
                    this.datacount = this.SENSOR_DATA.length;
                    if(this.datacount!=0){
                      this.dataSource.paginator = this.paginator;
                      this.dataSource.sort = this.sort;
                    }
                  }
                  this.activeUpdateEvent = false;
                  this._session.setEndQuery();
                  //request record-files 
                  this.requestRecorderFileInfo();
                }

                //LIST_RECORDER_FILES
                if(response.ACTION == 'LIST_RECORDER_FILES'){
                  console.log('LIST_RECORDER_FILES');
                  console.log(params);
                  this.RECORD_FILES = params;                  
                  this.activeUpdateEvent = false;
                  this._session.setEndQuery();
                }
                
                else{
                  console.log('message not processed:');
                  console.log(message);
                  this.activeUpdateEvent = false;
                  this._session.setEndQuery();
                }
                
              }catch(err){
                console.error('Error processing queue message:');
                console.error(err);
                this.activeUpdateEvent = false;
                this._session.setEndQuery();
              }
          });
          //send signal
          this.requestSensorList(this.device);
        }
    );
    
  }


  ngOnChanges(changes: SimpleChanges) {
    // changes.prop contains the old and the new value...
    let deviceEvent = changes['device'];
    console.log('changes')
    if(deviceEvent){
      let current:Device = changes['device'].currentValue;
      if(current !== undefined){
        this.device = current;        
      }
    }  
  }

  requestSensorList(device:Device): void{
    //check connection
    if(this._rxStomp.connected()){
      const command = JSON.stringify({ACTION:'LIST_SENSORS', PARAMS:''});  
      const uuid = this.device? this.device.uuid:'';  
      this._rxStomp.publish({destination : "/queue/".concat(uuid), body: command});
    }else{
      this.submitInfo.emit("Failed Connecting websocket-Server ");
    }
  }

  sendActiveSensor(sensor:Sensor){
    if(!this.activeUpdateEvent){
      this.activeUpdateEvent = true;
      if(this._rxStomp.connected()){
        const command = JSON.stringify({ACTION:'ACTIVATE_SENSOR', PARAMS:{uuid:sensor.uuid}});  
        const uuid = this.device? this.device.uuid:'';  
        this._rxStomp.publish({destination : "/queue/".concat(uuid), body: command});
      }else{
        this.submitInfo.emit("Failed Connecting websocket-Server ");
        this.activeUpdateEvent = false;
      }
    }    
  }

  sendDeactiveSensor(sensor:Sensor){
    if(!this.activeUpdateEvent){
      this.activeUpdateEvent = true;
      if(this._rxStomp.connected()){
        const command = JSON.stringify({ACTION:'DEACTIVATE_SENSOR', PARAMS:{uuid:sensor.uuid}});  
        const uuid = this.device? this.device.uuid:'';  
        this._rxStomp.publish({destination : "/queue/".concat(uuid), body: command});
      }else{
        this.submitInfo.emit("Failed Connecting websocket-Server ");
        this.activeUpdateEvent = false;
      }
    }    
  }

  sendAddSensor(sensor:Sensor){
    this._session.setShowQuery();
    if(!this.activeUpdateEvent){
      this.activeUpdateEvent = true;
      if(this._rxStomp.connected()){
        this._session.setShowQuery();
        const command = JSON.stringify({ACTION:'ADD_SENSOR', PARAMS:{sensor:sensor}});  
        const uuid = this.device? this.device.uuid:'';  
        this._rxStomp.publish({destination : "/queue/".concat(uuid), body: command});
      }else{
        this.submitInfo.emit("Failed Connecting websocket-Server ");
        this.activeUpdateEvent = false;
      }
    } 
  }

  sendDeleteSensor(sensor:Sensor){
    if(!this.activeUpdateEvent){
      this.activeUpdateEvent = true;
      if(this._rxStomp.connected()){
        this._session.setShowQuery();
        const command = JSON.stringify({ACTION:'REMOVE_SENSOR', PARAMS:{uuid:sensor.uuid}});  
        const uuid = this.device? this.device.uuid:'';  
        this._rxStomp.publish({destination : "/queue/".concat(uuid), body: command});
      }else{
        this.submitInfo.emit("Failed Connecting websocket-Server ");
        this.activeUpdateEvent = false;
      }
    } 
  }

  requestRecorderFileInfo(){
    if(!this.activeUpdateEvent){
      this.activeUpdateEvent = true;
      if(this._rxStomp.connected()){
        this._session.setShowQuery();
        const command = JSON.stringify({ACTION:'LIST_RECORDER_FILES', PARAMS:{}});  
        const uuid = this.device? this.device.uuid:'';  
        this._rxStomp.publish({destination : "/queue/".concat(uuid), body: command});
      }else{
        this.submitInfo.emit("Failed Connecting websocket-Server ");
        this.activeUpdateEvent = false;
      }
    } 
  }

  requestSensorInfo(): void {
    //Query data
    const uuid = this.device? this.device.uuid:'';
    this._request.sendBackendGet(this._backend.GET_DEVICE_SENSOR_URL(uuid)).subscribe({
      next:(res) => {
        let response:AgentMessage = <AgentMessage>JSON.parse(res);
        this.infoMessage =undefined;
        this.errorMessage =undefined;
        if(!response.error){
          this.infoMessage = response.message;
          this.SENSOR_DATA = response.response;
          this.dataSource = new MatTableDataSource<Sensor>(this.SENSOR_DATA);
          this.datacount = this.SENSOR_DATA.length;
          if(this.datacount!=0){
            this.dataSource.paginator = this.paginator;
            this.dataSource.sort = this.sort;
          }
          console.log("info: "+this.infoMessage);
          
        }else{
          this.showError = true;
          this.errorMessage = response.message;
          this.dataSource = new MatTableDataSource<Sensor>([]);
          console.log("error: "+this.infoMessage);
        }
      },
      error:(err) => {
        console.error("HTTP-Error requesting sensor data: "+err);
        this._session.setEndQuery();
      },
      complete:() =>{
        //hide progress bar
        this._session.setEndQuery();
        this.setInfoSubscriber();
      }
        });
  }

  

  /**
   * Disappear the Info header after 10 seconds
   */
  setInfoSubscriber() :void{
    //vanish InfoMessage if any
    if(this.infoMessage!==undefined){
      const secondsCounter = interval(10*1000);
      this.subscriber = secondsCounter.subscribe(
        n => {
          this.infoMessage = undefined; 
          this.subscriber.unsubscribe();
        }
      );
    }else{
      if(this.subscriber !== undefined){
        this.subscriber.unsubscribe();
      }
    }
  }

  reloadData():void{
    this._session.setShowQuery();
    this.requestSensorList(this.device);
  }

  removeInfoMssg(){
    this.infoMessage = undefined;
    this.showError = false;
  }
  removeErrorMssg(){
    this.errorMessage = undefined;
    this.showError = false;
  }

  submitSensor(event:any){
    console.log(event);
    const sensor = event as Sensor;
    this.sendAddSensor(sensor);
  }

  onEditEvent(message:string){
    this.openSnackBar(message);
    this.reloadData();
  }

  openSnackBar(message: any) {
    console.log(message);
    this._snackBar.open(message, 'dismiss',{
      duration: (7 * 1000),
    });
  }


}
