import { Injectable } from '@angular/core';
import { HttpClient, HttpHeaders, HttpResponse, HttpParams, HttpErrorResponse } from '@angular/common/http';
import { SessionService } from './session.service';
import { Observable, throwError } from 'rxjs';
import { catchError } from 'rxjs/operators';

@Injectable({
  providedIn: 'root'
})
export class RequestService {
  
  ResponseContentType: any = {
    Json: 'JSON',
    JsonHeader: 'application/json;odata=verbose;charset=utf-8',
    Xml:  'XML'
  };
  authHeader: any;

  constructor(private http: HttpClient, private session: SessionService) {
    const token = session.getSessionDeviceData()? JSON.stringify(session.getSessionDeviceData()?.token):'';
    this.authHeader = {'device':token, 'ContentType':this.ResponseContentType.JsonHeader};
   }

  /**
   *
   * @param url Send GET to backend
   */
  sendBackendGet(url: string, validacion?: string): Observable<any>{
    let headers = new HttpHeaders( this.authHeader);
    const httpOptions = {
      responseType: this.ResponseContentType.Json,
      headers: headers
    }; /** */

    return this.http.get( url, httpOptions)
    .pipe(
        catchError(this.handleError)
      );
  }
  
  /**
   * Send POST to backend
   */
  sendBackendPost(url: string, body: any, validacion?: string): Observable<any>{
    let headers = new HttpHeaders( this.authHeader);
    const httpOptions = {
      responseType: this.ResponseContentType.Json,
      headers: headers
    }; /** */

    return this.http.post(url, body, httpOptions)
    .pipe(
      catchError(this.handleError)
    );
  }

  /**
   * Send PUT to backend
   */
  sendBackendPut(url: string, body: any, validacion?: string): Observable<any>{
    let headers = new HttpHeaders(  this.authHeader);
    const httpOptions = {
      responseType: this.ResponseContentType.Json,
      headers: headers
    }; /** */

    return this.http.put(url, body, httpOptions)
    .pipe(
      catchError(this.handleError)
    );
  }

  /**
   *
   * @param url send DELETE to backend
   * @param body
   */
  sendBackendDelete(url: string, validacion?: string): Observable<any>{
    let headers = new HttpHeaders(  this.authHeader);
    const httpOptions = {
      responseType: this.ResponseContentType.Json,
      headers: headers
    };/** */
    return this.http.delete(url,httpOptions)
    .pipe(
      catchError(this.handleError)
    );

  }

  /**
   * handle errors from HTTP request
   */
  handleError(error: HttpErrorResponse) {
    if (error.error instanceof ErrorEvent) {
      // Access error
      console.error('Request operation failed.', error);
    }else{
      // Error response from backend server
      console.error(
        `Backend returned code ${error.status}, ` +
        `body was: ${error.error}`);
    }
    // Return an observable with a user-facing error message.
    return throwError( () => new Error(error.status + '-' + error.message) );
  }

}
