import { Component } from '@angular/core';
import { SessionService } from './shared/services/session.service';
import { RxStompService } from './shared/services/rx-stomp.service';

@Component({
  selector: 'app-root',
  templateUrl: './app.component.html',
  styleUrls: ['./app.component.css']
})
export class AppComponent {
  title = 'morpheusbox_admin';

  constructor(public _session:SessionService, private _rxStomp: RxStompService){

  }

  disconectDevice(event:any): void{
    console.log(event);
    this._session.closeSession();
  }

  getSessionDeviceUUID():string|undefined{
    var uuid;
    if(this._session.isSessionStarted()){
      const dev =this._session.getSessionDeviceData();
      uuid = dev?.uuid;
    }
    return uuid;
  }
}
