import { Injectable } from '@angular/core';

import { Router } from '@angular/router';
import { Subject, BehaviorSubject, Observable } from 'rxjs';
import { Device } from '../interfaces/device';
import { RxStompService } from './rx-stomp.service';

//singleton
export class SessionMngr {
  private device = new Subject<any>();
  public deviceObserver = this.device.asObservable();
  private sessview = new Subject<string>();
  public sessviewObserver = this.sessview.asObservable();

  private _queryMode: BehaviorSubject<boolean> = new BehaviorSubject<boolean>(false);
  public isOnQueryObs: Observable<boolean> = this._queryMode.asObservable();

  constructor(){
    var box = sessionStorage.getItem("MBX_CURRENTDEVICE");
    if(box != null && box != 'undefined'){
      let rawData: string = atob(box);
      const encoder = new TextEncoder();
      //rawData = Buffer.from(rawData,'base64').toString('utf8'); // Buffer.from(data).toString('base64');
      let data = JSON.parse(rawData)
      this.device.next(data);
    }
  }

  set ActiveDevice(val:Device|null) {
    //if null deletes all session data
    if(val == null){
      sessionStorage.clear();
    }else{
      //var encoded = Buffer.from(JSON.stringify(val)).toString('base64');
      var encoded =btoa(JSON.stringify(val))
      sessionStorage.setItem("MBX_CURRENTDEVICE", encoded);
      this.device.next(val);
    }    
  }

  get ActiveDevice(): Device|null {
    var box = sessionStorage.getItem("MBX_CURRENTDEVICE");
    if(box != null && box != 'undefined'){
      let rawData: string = atob(box);
      //rawData = Buffer.from(rawData,'base64').toString('utf8');
      let data = JSON.parse(rawData)
      this.device.next(data);

      return data;      
    }else{

      return null;
    }    
  }

  observerForActiveDevice(): Observable<any>{
    return this.deviceObserver;
  }

  sessionExist(): boolean{
    return !(sessionStorage.getItem("MBX_CURRENTDEVICE")===null);
  }

  setQueryMode():void{
    this._queryMode.next(true);
  }
  unsetQueryMode():void{
    this._queryMode.next(false);
  }

}
export const sessionMngr = new SessionMngr();

@Injectable({
  providedIn: 'root'
})
export class SessionService {

  public sessionManager = sessionMngr;
  public _rxStomp!: RxStompService;

  constructor(private _router:Router) { }

  startSession(device: Device, rxStomp:RxStompService|null =null) {
    this.sessionManager.ActiveDevice = device;
    console.log("Session created"+this.sessionManager.ActiveDevice);
    this._router.navigate([ {outlets: {primary:'connecteddevice'} } ]);
    if(rxStomp != null){
      this._rxStomp = rxStomp;
    }
  }

  getSessionDeviceData(): Device|null{
    return this.sessionManager.ActiveDevice;
  }

  isSessionStarted(): boolean{
    return this.sessionManager.sessionExist();
  }

  toHome():void {
    this._router.navigate([ {outlets: {primary:'home'} } ]);
  }

  toPath(path:string): void{
    this._router.navigate([ {outlets: {primary:path} } ]);
  }

  closeSession():void {
    this.sessionManager.ActiveDevice = null; 
    if(this._rxStomp != null){
      this._rxStomp.deactivate();
    }
    this.toHome();
  }

  setShowQuery() :void{
    this.sessionManager.setQueryMode();
  }
  setEndQuery(): void{
    this.sessionManager.unsetQueryMode();
  }

}
