import { Component, OnDestroy, OnInit, ViewChild } from '@angular/core';
import { MatPaginator } from '@angular/material/paginator';
import { MatSort } from '@angular/material/sort';
import { MatTableDataSource } from '@angular/material/table';
import { interval } from 'rxjs';
import { AgentMessage } from 'src/app/shared/interfaces/agent-message';
import { Device } from 'src/app/shared/interfaces/device';
import { BackendmapService } from 'src/app/shared/services/backendmap.service';
import { RequestService } from 'src/app/shared/services/request.service';
import { SessionService } from 'src/app/shared/services/session.service';
import { MatSnackBar } from '@angular/material/snack-bar';
import { RxStompService, rxStompServiceDestroy, rxStompServiceFactory } from 'src/app/shared/services/rx-stomp.service';


@Component({
  selector: 'app-devices-page',
  templateUrl: './devices-page.component.html',
  styleUrls: ['./devices-page.component.css'],
  providers: [{provide: RxStompService, useFactory: rxStompServiceFactory}]
})
export class DevicesPageComponent implements OnInit, OnDestroy {

  infoMessage: string|undefined;
  errorMessage: string|undefined;
  showError:boolean = false;
  showOffline:boolean = true;
  datacount: number = 0;
  subscriber!: any;

  DEVICE_DATA:Device[] = [];
  dataSource!: MatTableDataSource<Device>;
  @ViewChild(MatSort) sort!: MatSort;
  @ViewChild(MatPaginator) paginator!: MatPaginator;

  

  constructor(private _request: RequestService, private _backend:BackendmapService, private _snackBar: MatSnackBar,
    private _session: SessionService){
      
    }

    ngOnInit(){
      //Query list of registered devices
      this._request.sendBackendGet(this._backend.GET_DEVICES_LIST).subscribe({
        next:(res) =>{
          let response: AgentMessage = <AgentMessage>JSON.parse(res);
          this.infoMessage =undefined;
          this.errorMessage =undefined;
          if(response.error){
            this.showError = true;
            this.errorMessage = response.message;
            this.dataSource = new MatTableDataSource<Device>([]);
            console.error(response.message);
          }else{
            this.infoMessage = response.message;
            this.DEVICE_DATA = response.response;
            this.dataSource = new MatTableDataSource<Device>(this.DEVICE_DATA);
            this.datacount = this.DEVICE_DATA.length;
            if(this.datacount!=0){
              this.dataSource.paginator = this.paginator;
            }
            console.log("info: "+this.infoMessage);
          }
        },
        error:(err) =>{
        console.error("HTTP-Error requesting patient data: "+err);
        },
        complete:() =>{
          //hide progress bar
          this._session.setEndQuery();
          this.setInfoSubscriber();
        }
      });
    }

    ngOnDestroy() :void {
      rxStompServiceDestroy();
    }

    getPageFromDataSource(): Device[]{
      if(this.dataSource != undefined){
        let deviceList = this.dataSource._pageData(this.DEVICE_DATA);
        return deviceList;
      }
      return [];
    }

    /**
     * Disappear the Info header after 10 seconds
     */
    setInfoSubscriber() :void{
      //vanish InfoMessage if any
      if(this.infoMessage!==undefined){
        const secondsCounter = interval(7*1000);
        this.subscriber = secondsCounter.subscribe(
          n => {
            this.infoMessage = undefined; 
            this.subscriber.unsubscribe();
            console.log(n+" end subscriber");
          }
        );
      }else{
        if(this.subscriber !== undefined){
          this.subscriber.unsubscribe();
        }
      }
    }

    removeInfoMssg(){
      this.infoMessage = undefined;
      this.showError = false;
    }
    removeErrorMssg(){
      this.errorMessage = undefined;
      this.showError = false;
    }

    onMessageEvent(message:string): void{
      this._snackBar.open(message, 'dismiss',{
        duration: (7 * 1000),
      });
    }

}
