import { Component, Input, OnChanges, SimpleChanges, EventEmitter, Output } from '@angular/core';
import { Device } from '../../interfaces/device';

@Component({
  selector: 'app-recording-controls',
  templateUrl: './recording-controls.component.html',
  styleUrls: ['./recording-controls.component.css']
})
export class RecordingControlsComponent implements OnChanges{

 
  /**
   * Configures how the launcher will be rendered
   * possible values: picture (default) | button
   */
  @Input() mode:string = "picture";
  /**
   * Device object with all the elements describing the device 
   */
  @Input() device!:Device | null;
  /**
   * Defines if a recording is in progress
   */
  @Input('isRecording') recordingInProgress:boolean = false;

  @Output() submitObject: EventEmitter<string> = new EventEmitter();
  @Output() startEvent: EventEmitter<string> = new EventEmitter();
  @Output() stopEvent: EventEmitter<string> = new EventEmitter();



  ngOnChanges(changes: SimpleChanges): void {
    // changes.prop contains the old and the new value...
    let device = changes['device'];
    let isRecording = changes['isRecording'];
    if(device){

    }else 
    if(isRecording){
      let log:string = isRecording.currentValue?"recording in progress": "recording stopped";
      console.log(log);
    }else{

    }
  }

  startRecording(event:any):void{
    this.startEvent.emit(event);
  }

  stopRecording(event:any):void{
    this.stopEvent.emit(event);
  }

  showRecordingInfo():void{

  }

}
