import { Component } from '@angular/core';
import { MatSnackBar } from '@angular/material/snack-bar';
import { Device } from 'src/app/shared/interfaces/device';
import { SessionService } from 'src/app/shared/services/session.service';

@Component({
  selector: 'app-recordings-page',
  templateUrl: './recordings-page.component.html',
  styleUrls: ['./recordings-page.component.css']
})
export class RecordingsPageComponent {

  activeDevice:Device|null;

  constructor(private _snackBar: MatSnackBar, public _session: SessionService){
    this.activeDevice = this._session.getSessionDeviceData();
  }

  openSnackBar(message: any) {
    console.log(message);
    this._snackBar.open(message, 'dismiss',{
      duration: (7 * 1000),
    });
  }

}
