import { Component, EventEmitter, Input, OnChanges, OnInit, Output, SimpleChanges } from '@angular/core';
import { FormBuilder, FormControl, FormGroup } from '@angular/forms';
import {Observable, Subscription, interval} from 'rxjs';
import {map, startWith} from 'rxjs/operators';
import { RequestService } from '../../services/request.service';
import { BackendmapService } from '../../services/backendmap.service';
import { Device } from '../../interfaces/device';
import { SessionService } from '../../services/session.service';
import { GlobalUtilService } from '../../services/global-util.service';
import { AgentMessage } from '../../interfaces/agent-message';
import { Patient } from '../../interfaces/patient';
import { MatTableDataSource } from '@angular/material/table';
import { RxStompService, rxStompServiceFactory } from '../../services/rx-stomp.service';
import { Message } from '@stomp/stompjs';

@Component({
  selector: 'app-edit-device-form',
  templateUrl: './edit-device-form.component.html',
  styleUrls: ['./edit-device-form.component.css'],
  providers: [{provide: RxStompService, useFactory: rxStompServiceFactory}]
})
export class EditDeviceFormComponent implements OnInit, OnChanges {

  @Input('device') device!: Device|null;
  @Output() submitInfo: EventEmitter<string> = new EventEmitter();

  
  infoMessage: string|undefined;
  errorMessage: string|undefined;
  showError:boolean = false;
  subscriber!: any;
  errorEvent:boolean = false;
  isNewDevice:boolean = false;

  brokerFail:boolean = false;
  stompSubscriber!:Subscription;
  queueSubscription!:Subscription;

  deviceForm!:FormGroup;
  PATIENT_DATA: Patient[] = [];
  patientDataSource!: MatTableDataSource<Patient>;
  filteredPatients!: Observable<Patient[]>;
  patientSelectorFC:FormControl = new FormControl();
  isPatientAssigned:boolean = true;

  typesFC:FormControl = new FormControl();
  deviceTypes:string[] = ["bed"];

  constructor(private _rxStomp: RxStompService, private _formBuilder: FormBuilder, private _request: RequestService, private _backend:BackendmapService, 
                private _session: SessionService, public _util:GlobalUtilService){
      if(_session.isSessionStarted()){
        this.device = this._session.getSessionDeviceData();
      }else{
        this._session.toHome();
      }
  }

  ngOnInit() {
    console.log(this.device);
    this.getPatientData();
    this.initDeviceForm();
    
    this.stompSubscriber = this._rxStomp.connected$
      .subscribe(
        state =>{
          console.log('state');
          console.log(state);
          //assign flag
          this.brokerFail = false;
          const uuid = this.device?this.device.uuid:'';
          //create subscription
          this.queueSubscription = this._rxStomp
          .watch('/topic/'.concat(uuid))
          .subscribe((message: Message) => {
              console.log('message recieved:');
              console.log(message);
              try{
                const response = JSON.parse(message.body);
                const params = response.PARAMS;

                //ONLINE_SIGNAL
                if(response.ACTION == 'ONLINE_SIGNAL'){
                  if(this.device!=null){
                    this.device.isOnline = (params == uuid);
                  }
                }else{
                  console.log('message not processed:');
                  console.log(message);
                }
                
              }catch(err){
                console.error('Error processing queue message:');
                console.error(err);
              }
          });
        }
      );
    
  }

  ngOnDestroy() {
    //unbind the subscriber
    if(this.queueSubscription !== undefined){
      this.queueSubscription.unsubscribe();
    }
    if(this.queueSubscription !== undefined){
      this.stompSubscriber.unsubscribe();
    }
  }


  ngOnChanges(changes: SimpleChanges) {
    // changes.prop contains the old and the new value...
    let deviceEvent = changes['device'];
    if(deviceEvent){
      let current:Device = changes['device'].currentValue;
      if(current !== undefined){
        this.initDeviceForm();
      }
      this.isNewDevice=false;
    }    
  }

  initDeviceForm():void{
    if(this.device){
      this.isNewDevice=false;

      //autocomplete formControls
      this.patientSelectorFC = new FormControl(this.device.patientDto.alias);
      this.typesFC = new FormControl(this.device.type);

      //Form group form controls
      this.deviceForm = new FormGroup({
        uuid: new FormControl({value:this.device.uuid, disabled: true}),
        type: this.typesFC,
        name: new FormControl(this.device.name),
        xBedDimension: new FormControl(this.device.xBedDimension),
        yBedDimension: new FormControl(this.device.yBedDimension),
        bedDimensionUnits: new FormControl(this.device.bedDimensionUnits),
        patient_uuid: new FormControl(this.device.patientUuid),
        patientDto: new FormControl(this.device.patientDto),
        patient: new FormControl(this.device.patientDto),
      });
    }else{
      this.isNewDevice = true;
      this.deviceForm = new FormGroup({
        uuid: new FormControl(''),
        type: new FormControl(''),        
        name: new FormControl(''),        
        xBedDimension: new FormControl(''),        
        yBedDimension: new FormControl(''),        
        bedDimensionUnits: new FormControl('centimeters'),
        patient_uuid: new FormControl(),
        patientDto: new FormControl(),
        patient: new FormControl()
      });
    }//else
  }

  /**
   * Query the Patient data registered in the server
   */
  getPatientData(): void {
    //Query data
    this._request.sendBackendGet(this._backend.GET_PATIENT_DATA).subscribe({
      next:(res) => {
        let response:AgentMessage = <AgentMessage>JSON.parse(res);
        this.infoMessage =undefined;
        this.errorMessage =undefined;

        if(!response.error){
          this.infoMessage = response.message;
          this.PATIENT_DATA = response.response;
          this.patientDataSource = new MatTableDataSource<Patient>(this.PATIENT_DATA);

          //set value for current patient
          this.patientSelectorFC.setValue(this.device?.patientDto.name, {emitEvent:true});

          //autocomplete for patient list
          this.filteredPatients = this.patientSelectorFC.valueChanges
          .pipe(
            startWith(''),
            map(value => this._filterPatient(value))
          );
          console.log("info: "+this.infoMessage);
          
        }else{
          this.showError = true;
          this.errorMessage = response.message;
          this.patientDataSource = new MatTableDataSource<Patient>([]);
          console.log("error: "+this.infoMessage);
        }
      },
      error:(err) => {
        console.error("HTTP-Error requesting patient data: "+err);
      },
      complete:() =>{
        //hide progress bar
        this._session.setEndQuery();
        this.setInfoSubscriber();
      }
        });

  }

  private _filterPatient(value: any): Patient[] {
    try{
      const filterValue = value.toLowerCase();
      this.patientDataSource.filter = value;
      return this.patientDataSource.filteredData;
      
    }catch(err){
      return this.patientDataSource.data;
    }
  }

  actualSubmit(data:any){
    this.errorEvent = false;
    this._session.setShowQuery();
    this._request.sendBackendPut(this._backend.PUT_DEVICE_DATA,data).subscribe({
      next: (res) =>{
        let response:AgentMessage = <AgentMessage>JSON.parse(res);
        if(response.error){
          this.showError = true;
          this.errorMessage = response.message;
          console.error(response.message);

        }else{          
          this.submitInfo.emit(response.message);
          //send sync order to device
          const uuid = this.device?this.device.uuid:'';
          if(this._rxStomp.connected()){
            const command = JSON.stringify({ACTION:'SYNC_DEVICE', PARAMS:''});    
            this._rxStomp.publish({destination : "/queue/".concat(uuid), body: command});
          }else{
            this.showError = true;
            this.errorMessage = "Failed Connecting websocket-Server";
          }
        }
      },
      error: (err) =>{
        this.showError = true;
        this.errorMessage = err;
        console.log("HTTP-Error requesting patient data: "+err);
      },
      complete: () =>{
        this._session.setEndQuery();
      }
    });
  }

  stringifyPatientOption(patient?:any):string{
    if(typeof patient === "string"){
      return patient;
    }else{
      const alias = patient? patient.alias? patient.alias: patient.name : undefined;
      return alias;
    }
  }

  setPatient(option:any):void{
    if(option.source.value){
      const patient = option.source.value;
      const patient_uuid = patient.uuid;
      //restricted patient data is assigned to the device to promote privacy
      const patientDto = {uuid:patient_uuid,name:patient.alias};
      let patientControl = new FormControl(patientDto);
      let uuidControl = new FormControl(patient_uuid);
      this.deviceForm.setControl("patientDto", patientControl,{emitEvent:true});
      this.deviceForm.setControl("patient_uuid", uuidControl,{emitEvent:true});
      this.deviceForm.setControl("patient", uuidControl,{emitEvent:true});
    }    
  }

  onSubmit(){
    if(!this.deviceForm.invalid){
      let data = this.deviceForm.getRawValue();
      this.actualSubmit(data);
    }
  }

  removeInfoMssg(){
    this.infoMessage = undefined;
    this.showError = false;
  }
  removeErrorMssg(){
    this.errorMessage = undefined;
    this.showError = false;
  }

  resetForm(){
    this.initDeviceForm();
    this.patientSelectorFC.setValue(this.device?.patientDto.name, {emitEvent:true});
  }

  /**
   * Disappear the Info header after 10 seconds
   */
  setInfoSubscriber() :void{
    //vanish InfoMessage if any
    if(this.infoMessage!==undefined){
      const secondsCounter = interval(10*1000);
      this.subscriber = secondsCounter.subscribe(
        n => {
          this.infoMessage = undefined; 
          this.subscriber.unsubscribe();
        }
      );
    }else{
      if(this.subscriber !== undefined){
        this.subscriber.unsubscribe();
      }
    }
  }


}
