import { NgModule } from '@angular/core';
import { RouterModule, Routes } from '@angular/router';
import { PatientsPageComponent } from './pages/patients-page/patients-page.component';
import { DevicesPageComponent } from './pages/devices-page/devices-page.component';
import { SensorsPageComponent } from './pages/sensors-page/sensors-page.component';
import { RecordingsPageComponent } from './pages/recordings-page/recordings-page.component';
import { EditDeviceFormComponent } from './shared/components/edit-device-form/edit-device-form.component';

const routes: Routes = [
  {path: '', redirectTo: 'patients', pathMatch: 'full'},
  {path: 'home', component: DevicesPageComponent},
  {path: 'patients', component: PatientsPageComponent},
  {path: 'devices', component: DevicesPageComponent},
  {path: 'connecteddevice', component: EditDeviceFormComponent},
  {path: 'sensors', component: SensorsPageComponent},
  {path: 'recordings', component: RecordingsPageComponent},

];

@NgModule({
  imports: [RouterModule.forRoot(routes)],
  exports: [RouterModule]
})
export class AppRoutingModule { }
