import { Component, EventEmitter, Input, OnChanges, Output, SimpleChanges } from '@angular/core';
import {FormBuilder, FormControl, FormGroup} from '@angular/forms';
import { NgbModalConfig, NgbModal, NgbModalRef } from '@ng-bootstrap/ng-bootstrap';
import { GlobalUtilService } from '../../services/global-util.service';
import { Patient } from '../../interfaces/patient';
import { RequestService } from '../../services/request.service';
import { BackendmapService } from '../../services/backendmap.service';
import { AgentMessage } from '../../interfaces/agent-message';
import { SessionService } from '../../services/session.service';

@Component({
  selector: 'app-link-patient-form',
  templateUrl: './link-patient-form.component.html',
  styleUrls: ['./link-patient-form.component.css']
})
export class LinkPatientFormComponent implements OnChanges {

  @Input('mode') mode: string ="fab";
  @Input('title') title: string = "Edit Patient Information";
  @Input('patient') patient!: Patient;

  @Output() submitInfo: EventEmitter<string> = new EventEmitter();

  warnMessage: string = "Warning";
  errorEvent:boolean = false;
  editHunit:boolean =false;
  editWunit:boolean =false;

  modalReference!:NgbModalRef;
  patientForm!:FormGroup;


  constructor(config: NgbModalConfig, private modalService: NgbModal, 
          private _formBuilder: FormBuilder,
          private _request: RequestService, private _backend:BackendmapService,
          private _session: SessionService, public _util:GlobalUtilService) {
		// customize default values of modals used by this component tree
		config.backdrop = 'static';
		config.keyboard = false;
    this.initPatientForm();
	}

  ngOnChanges(changes: SimpleChanges) {
    // changes.prop contains the old and the new value...
    let patientEvent = changes['patient'];
    if(patientEvent){
      let current:Patient = changes['patient'].currentValue;
      if(current !== undefined){
        this.initPatientForm();
      }
    }    
  }

  modalOpen(content: any) {
		this.modalReference = this.modalService.open(content, { fullscreen: true });
	}

  modalClose(){
    this.patientForm.markAsUntouched();
    this.initPatientForm();
    this.modalReference.close()
    //this.modalService.dismissAll('finished');
  }

  initPatientForm():void{
    if(this.patient !== undefined){
      this.patientForm = new FormGroup({
        uuid: new FormControl(this.patient.uuid),
        name: new FormControl(this.patient.name),
        alias: new FormControl(this.patient.alias),
        age: new FormControl(this.patient.age),
        height: new FormControl(this.patient.height),
        heightUnit: new FormControl(this.patient.heightUnit),
        weight: new FormControl(this.patient.weight),
        weightUnit: new FormControl(this.patient.weightUnit)
      });
    }else{
      this.patientForm = new FormGroup({
        uuid: new FormControl(''),
        name: new FormControl(''),
        alias: new FormControl(''),
        age: new FormControl(),
        height: new FormControl(),
        heightUnit: new FormControl('centimeters'),
        weight: new FormControl(),
        weightUnit: new FormControl('kilograms')
      });
    }//else
  }

  getHUnitValue():string{
    let form = this.patientForm.getRawValue();
    const unit = form['heightUnit'];
    if(unit ===null){
      return "centimeters"
    }else{
      return unit;
    }
  }

  getWUnitValue():string{
    let form = this.patientForm.getRawValue();
    const unit = form['weightUnit'];
    if(unit ===null){
      return "kilograms"
    }else{
      return unit;
    }
  }

  showUnits(name:string):void{
      if(name == 'hunit'){
        this.editHunit = !this.editHunit;
      }
      if(name == 'wunit'){
        this.editWunit = !this.editWunit;
      }
      console.log(name);
  }

  unitChangerLabel(name:string): string{
    if(name == 'hunit'){
      return this.editHunit?'set':'Change units';
    }
    if(name == 'wunit'){
      return this.editWunit?'set':'Change units';
    }
    return 'Change units';
  }

  actualSubmit(data:any){
    this.errorEvent = false;
    this._session.setShowQuery();
    this._request.sendBackendPost(this._backend.POST_PATIENT_DATA,data).subscribe({
      next: (res) =>{
        let response:AgentMessage = <AgentMessage>JSON.parse(res);
        if(response.error){
          this.errorEvent = true;
          this.warnMessage = response.message;
          console.error(response.message);

        }else{          
          this.submitInfo.emit(response.message);
          this.patientForm.reset();
          this.modalService.dismissAll('finished')
        }
      },
      error: (err) =>{
        this.errorEvent = true;
        this.warnMessage = err;
        console.log("HTTP-Error requesting patient data: "+err);
      },
      complete: () =>{
        this._session.setEndQuery();
      }
    });
  }

  onSubmit(){
    if(!this.patientForm.invalid){
      let data = this.patientForm.getRawValue();
      this.actualSubmit(data);
    }
  }
}
