import { Component, EventEmitter, Input, OnChanges, Output, SimpleChanges } from '@angular/core';
import { Sensor } from '../../interfaces/sensor';
import { NgbModal, NgbModalConfig } from '@ng-bootstrap/ng-bootstrap';
import { BackendmapService } from '../../services/backendmap.service';
import { GlobalUtilService } from '../../services/global-util.service';
import { SessionService } from '../../services/session.service';

@Component({
  selector: 'app-del-sensor-button',
  templateUrl: './del-sensor-button.component.html',
  styleUrls: ['./del-sensor-button.component.css']
})
export class DelSensorButtonComponent implements OnChanges {

  @Input('sensor') sensor!: Sensor;
  @Input('success') success!: boolean;
  @Input('error') error!: boolean;
  @Output() submitInfo: EventEmitter<string> = new EventEmitter();
  @Output() confirmDeletion: EventEmitter<Sensor> = new EventEmitter();

  warnMessage: string = "Warning";
  additional_message:string = "";
  errorEvent:boolean = false;

  constructor(config: NgbModalConfig, private modalService: NgbModal, private _session: SessionService, public _util:GlobalUtilService) {
    // customize default values of modals used by this component tree
    config.backdrop = 'static';
    config.keyboard = false;
  }

  ngOnChanges(changes: SimpleChanges): void {
    // changes.prop contains the old and the new value...
    let confirmEvent = changes['success'];
    let error = changes['error'];
    if(confirmEvent || error){
      this.modalClose();  
    }
  }

  modalOpen(content: any) {
		this.modalService.open(content, { centered: true, size: 'lg' });
	}

  modalClose(){
    this.modalService.dismissAll('finished');
    this._session.setEndQuery();
  }

  /**
   * Send a REST delete
   */
  requestDelete():void{
    //show query bar
    this._session.setShowQuery();
    //send STOMP request from parent
    this.confirmDeletion.next(this.sensor);
  }

}
