import { Injectable } from '@angular/core';
import { environment } from 'src/environments/environment';

@Injectable({
  providedIn: 'root'
})
export class BackendmapService {


  private BASE_URL: string = environment.backend_url;

  /** Patient infromation */
  GET_PATIENT_DATA = this.BASE_URL.concat("/users/patients");
  POST_PATIENT_DATA = this.BASE_URL.concat("/users/patient");
  private DELETE_PATIENT_DATA = this.BASE_URL.concat("/users/delete/");//{uuid}
  public DELETE_PATIENT_URL(uuid:string):string{
    return this.DELETE_PATIENT_DATA.concat(uuid);
  }
  GET_RANDOM_UUID_URL = this.BASE_URL.concat("/users/randomuuid");
  /** Devices information */
  GET_DEVICES_LIST = this.BASE_URL.concat("/devices/dto");
  POST_DEVICE_DATA = this.BASE_URL.concat("/devices/add");
  PUT_DEVICE_DATA = this.BASE_URL.concat("/devices/add");
  /** Sensor Information */
  private GET_SENSOR_LIST = this.BASE_URL.concat("/devices/sensors");//{uuid}
  public GET_DEVICE_SENSOR_URL(uuid:string):string{
    return this.GET_SENSOR_LIST.concat("/"+uuid);
  }
  /** Recording Information */
  public GET_RECORDING_LIST = this.BASE_URL.concat("/recordings/");//device/{deviceuuid}
  public GET_DEVICE_RECORDINGS_URL(device_uuid:string):string{
    return this.GET_RECORDING_LIST.concat('device/').concat(device_uuid);
  }
  private GET_DATARECORDS = this.BASE_URL.concat("/recordings/datarecords/zip/");//{recuuid}
  public GET_RAW_DATARECORDS_URL(recording_uuid:string){
    return this.GET_DATARECORDS.concat("raw/").concat(recording_uuid);
  }
  public GET_FORMAT_DATARECORDS_URL(format:string,recording_uuid:string){
    return this.GET_DATARECORDS.concat(format).concat("/").concat(recording_uuid);
  }

  constructor() { }
}
