import { Component, Input, OnChanges, SimpleChanges, EventEmitter, Output } from '@angular/core';
import { NgbModal } from '@ng-bootstrap/ng-bootstrap';
import { Recording } from '../../interfaces/recording';
import { BackendmapService } from '../../services/backendmap.service';

@Component({
  selector: 'app-link-download-data-form',
  templateUrl: './link-download-data-form.component.html',
  styleUrls: ['./link-download-data-form.component.css']
})
export class LinkDownloadDataFormComponent implements OnChanges {

  /**
   * Configures how the launcher will be rendered
   * possible values: fab (default) | button
   */
  @Input() mode:string = "fab";

  @Input() recording!:Recording;

  @Output() submitObject: EventEmitter<string> = new EventEmitter();

  format:any = "raw"

  constructor(private modalService: NgbModal, private _backend:BackendmapService){
    //config.
  }

  ngOnChanges(changes: SimpleChanges) {
    // changes.prop contains the old and the new value...
    let recording = changes['recording'];
    if(recording){
    }
    console.log(changes);
  }

  showModal(content:any){
    this.modalService.open(content, {   scrollable: true, centered: true, size:'lg' });
  }

  openFiletab(format:string): void{
    let recording_uuid:string = this.recording.uuid;
    window.open(this._backend.GET_FORMAT_DATARECORDS_URL(format,recording_uuid), '_blank');
  }

}
