import { AfterContentInit, Component, Input, OnChanges, OnDestroy, SimpleChanges, EventEmitter, Output, OnInit, ViewChild } from '@angular/core';
import { Message } from '@stomp/stompjs';
import { Device } from '../../interfaces/device';
import {  interval, Subscription } from 'rxjs';
import { GlobalUtilService } from '../../services/global-util.service';
import { SessionService } from '../../services/session.service';
import { RxStompService } from '../../services/rx-stomp.service';

@Component({
  selector: 'app-device-card',
  templateUrl: './device-card.component.html',
  styleUrls: ['./device-card.component.css']
})
export class DeviceCardComponent implements OnInit, OnChanges, AfterContentInit, OnDestroy {

  @Input('device') device!: Device;
  @Output() submitInfo: EventEmitter<string> = new EventEmitter();

  aliveSubscriber!: Subscription;
  querySubscriber!: Subscription;
  stompSubscriber!: Subscription;
  /**
   * Subscription for the queue/device_uuid. Initialized after the rxStomp connection
   */
  queueSubscription!: Subscription;
  /**
   * Subscription for the new online devices topic. Initialized after the rxStomp connection
   */
  newOnlineSubscription!: Subscription;
  /**
   * The number of failed attempts before quiting auto-query device
   */
  queryCounterThreshold:number =1;

  actionControls:boolean = false;
  recordingInProgress:boolean = false;
  queryInProgress:boolean = false;
  brokerFail:boolean = false;

  constructor(private _rxStomp: RxStompService, private _session: SessionService, public _util:GlobalUtilService) {
  }

  ngOnInit() {
    this.stompSubscriber = this._rxStomp.connected$
      .subscribe(
        state =>{
          //assign flag
          this.brokerFail = false;
          //create subscription
          this.queueSubscription = this._rxStomp
          .watch('/topic/'.concat(this.device.uuid))
          .subscribe((message: Message) => {
              console.log('message recieved:');
              console.log(message);
              try{
                const response = JSON.parse(message.body);
                const params = response.PARAMS;

                //ONLINE_SIGNAL
                if(response.ACTION == 'ONLINE_SIGNAL'){
                  this.device.isOnline = (params == this.device.uuid);
                }else 
                //CONNECTION_TOKEN
                /*if(response.ACTION == 'CONNECTION_TOKEN'){
                  this.device.token = params;
                }else/** */
                //RECORDING_STATUS
                if(response.ACTION == 'RECORDING_STATUS'){
                  this.recordingInProgress = params;
                  this.device.isOnline =true;
                  console.log(params);
                }                
                else{
                  console.log('message not processed:');
                  console.log(message);
                }
                //destroys query subscriber to stop sending alive request
                if(this.querySubscriber !== undefined){
                  this.querySubscriber.unsubscribe();
                  this.queryInProgress = false;
                }
              }catch(err){
                console.error('Error processing queue message:');
                console.error(err);
              }
          });
          //subscribe the online topic to identify late online devices
          this.newOnlineSubscription = this._rxStomp
          .watch('/topic/online_devices').subscribe(
            (message: Message) =>{
              console.log('report_online message recieved:');
              console.log(message);
              try{
                const newDevice = JSON.parse(message.body);
                this.device.isOnline = (newDevice.uuid == this.device.uuid);

              }catch(err){
                console.error('Error processing report_online message:');
                console.error(err);
              }

          });
          //ping the device
          this.pingDevice(this.device);
          this.requestConnectionToken(this.device);
        }
      );
  }


  ngOnChanges(changes: SimpleChanges) {
    // changes.prop contains the old and the new value...
    let deviceEvent = changes['device'];
    if(deviceEvent){
      let current:Device = changes['device'].currentValue;
      if(current !== undefined){
        this.device = current;
      }
    }  
  }

  ngAfterContentInit() {
    const secondsCounter = interval(30*1000);
    this.aliveSubscriber = secondsCounter.subscribe(
      n => {
        if(this.device !== undefined){
          console.log("checking device");
          this.pingDevice(this.device);
        }else{
          console.log("device-undefined");
        }
        if(n>=this.queryCounterThreshold){
          this.aliveSubscriber.unsubscribe();
          console.log('quiting auto-ping. Number of attempts: '+(n+1));
        } 
      });
  }

  ngOnDestroy() {
    //unbind the subscriber
    this.aliveSubscriber.unsubscribe();
    this.queueSubscription.unsubscribe();
    this.stompSubscriber.unsubscribe();
    this.newOnlineSubscription.unsubscribe();
  }

  pingDevice(device:Device): void{
    //check connection
    if(this._rxStomp.connected()){
      const command = this.recordingInProgress?JSON.stringify({ACTION:'RECORDING_STATUS', PARAMS:''}):JSON.stringify({ACTION:'ONLINE_SIGNAL', PARAMS:''});    
      this._rxStomp.publish({destination : "/queue/".concat(this.device.uuid), body: command});
      this.startQueryCounter(this.device.uuid);
    }else{
      this.submitInfo.emit("Failed Connecting websocket-Server ");
    }
  }

  requestRecordingStatus(device:Device): void{
    //check connection
    if(this._rxStomp.connected()){
      const command = JSON.stringify({ACTION:'RECORDING_STATUS', PARAMS:''});    
      this._rxStomp.publish({destination : "/queue/".concat(device.uuid), body: command});
      this.startQueryCounter(this.device.uuid);
    }else{
      this.submitInfo.emit("Failed Connecting websocket-Server ");
    }
  }

  requestConnectionToken(device:Device): void{
    //check connection
    if(this._rxStomp.connected()){
      const command = JSON.stringify({ACTION:'CONNECTION_TOKEN', PARAMS:''});    
      this._rxStomp.publish({destination : "/queue/".concat(device.uuid), body: command});
    }else{
      this.submitInfo.emit("Failed Connecting websocket-Server ");
    }
  }

  startQueryCounter(queueName:string = '') :void{
    this.queryInProgress = true;
    const queryCounter = interval(5*1000);
    this.querySubscriber = queryCounter.subscribe(
      n => {
        this.queryInProgress = false;
        this.querySubscriber.unsubscribe();
        console.log('time-off: no response from device:'+queueName);
        this.device.isOnline = false;
        this.submitInfo.emit('Device '+this.device.uuid+' is offline');
      });
  }

  showActionControls(): void{
    this.requestRecordingStatus(this.device);
    this.actionControls = this.recordingInProgress? this.recordingInProgress: !this.actionControls;
  }

  toRecordings():void{
    this.connectDevice();
    this._session.toPath('recordings');
  }

  connectDevice(): void{
    this._session.startSession(this.device, this._rxStomp);
    //this._router.navigate([ {outlets: {primary:'connecteddevice'} } ])
  }

  startRecording(): void{
    if(this._rxStomp.connected()){
      const command = JSON.stringify({ACTION:'START_RECORDING', PARAMS:''});    
      this._rxStomp.publish({destination : "/queue/".concat(this.device.uuid), body: command});
      this.requestRecordingStatus(this.device);
    }else{
      this.submitInfo.emit("Failed Connecting websocket-Server ");
    }
  }

  stopRecording(): void{
    if(this._rxStomp.connected()){
      const command = JSON.stringify({ACTION:'STOP_RECORDING', PARAMS:''});    
      this._rxStomp.publish({destination : "/queue/".concat(this.device.uuid), body: command});
      this.requestRecordingStatus(this.device);
    }else{
      this.submitInfo.emit("Failed Connecting websocket-Server ");
    }
  }

  showRecordingInfo(): void{}

}
