import { Recorder } from "../interfaces/sensor";

export class PtzSensor {

    uuid: string;
    name: string;
    transducerType:string;
    physicalDimension:string;
    physicalMin:number;
    physicalMax:number;
    digitalMin:number;
    digitalMax:number;
    xLocation:number;
    yLocation:number;
    yDimension:string;
    xDimension:string;
    active:boolean;
    accessProtocol:string;
    portAddress:string;
    recorder:Recorder;
    

    constructor(){
        this.uuid = "";
        this.name = "";
        this.transducerType = "PTZ";
        this.physicalDimension = "V";
        this.physicalMin = -5;
        this.physicalMax = 5;
        this.digitalMin = -2048;
        this.digitalMax = 2047;
        this.xLocation = 0;
        this.yLocation = 0;
        this.yDimension = "5";
        this.xDimension = "5";
        this.active = true;
        this.accessProtocol = "I2C";
        this.portAddress = "0";
        this.recorder = {
            command: "",
            streamer: false,
            filepath:"",
            description:""
        };
    }
}
