import { AfterViewInit, Component, EventEmitter, Input, OnChanges, OnInit, Output, SimpleChanges, ViewChild } from '@angular/core';
import {MatPaginator} from '@angular/material/paginator';
import {MatTableDataSource} from '@angular/material/table';
import { SessionService } from '../../services/session.service';
import { BackendmapService } from '../../services/backendmap.service';
import { RequestService } from 'src/app/shared/services/request.service';
import { AgentMessage } from '../../interfaces/agent-message';
import { MatSort } from '@angular/material/sort';
import { interval } from 'rxjs';
import { MatSnackBar } from '@angular/material/snack-bar';
import { Device } from '../../interfaces/device';
import { Recording } from '../../interfaces/recording';

@Component({
  selector: 'app-recordings-data-table',
  templateUrl: './recordings-data-table.component.html',
  styleUrls: ['./recordings-data-table.component.css']
})
export class RecordingsDataTableComponent implements OnInit, OnChanges, AfterViewInit {

  @Input('device') device!: Device|null;
  @Output() submitInfo: EventEmitter<string> = new EventEmitter();

  RECORDING_DATA: Recording[] = [];
  infoMessage: string|undefined;
  errorMessage: string|undefined;
  showError:boolean = false;
  datacount: number = 0;
  subscriber!: any;

  displayedColumns: string[] = ['uuid','registeredDevice', 'startDate', 'stopdate', 'patient','controls'];
  dataSource!: MatTableDataSource<Recording>;

  @ViewChild(MatSort) sort!: MatSort;
  @ViewChild(MatPaginator) paginator!: MatPaginator;

  constructor(private _request: RequestService, public _session:SessionService, 
    private _snackBar: MatSnackBar, private _backend:BackendmapService){

  }

  ngOnInit(): void {
    this.requestRecordingInfo();
  }

  ngAfterViewInit() {
    if(!this.device){

    }
  }

  ngOnChanges(changes: SimpleChanges) {
    // changes.prop contains the old and the new value...
    let deviceEvent = changes['device'];
    if(deviceEvent){console.log('deviceEvent:'); console.log(deviceEvent);
      let current:Device = changes['device'].currentValue;
      if(current !== undefined && current !== null){
        this.device = current;
        this.displayedColumns= ['uuid', 'startDate', 'stopdate', 'patient','controls'];
      }else{
        this.displayedColumns= ['uuid','registeredDevice', 'startDate', 'stopdate', 'patient','controls'];
      }      
    }
  }

  requestRecordingInfo(): void {
    const recordings_end_point:string = this.device!==null? this._backend.GET_DEVICE_RECORDINGS_URL(this.device.uuid): this._backend.GET_RECORDING_LIST;
    //Query data
    this._request.sendBackendGet(recordings_end_point).subscribe({
      next:(res) => {
        let response:AgentMessage = <AgentMessage>JSON.parse(res);
        this.infoMessage =undefined;
        this.errorMessage =undefined;
        if(!response.error){
          this.infoMessage = response.message;
          this.RECORDING_DATA = response.response;
          this.dataSource = new MatTableDataSource<Recording>(this.RECORDING_DATA);
          this.datacount = this.RECORDING_DATA.length;
          if(this.datacount!=0){
            this.dataSource.paginator = this.paginator;
            this.dataSource.sort = this.sort;
          }
          console.log("info: "+this.infoMessage);
          
        }else{
          this.showError = true;
          this.errorMessage = response.message;
          this.dataSource = new MatTableDataSource<Recording>([]);
          console.log("error: "+this.infoMessage);
        }
      },
      error:(err) => {
        console.error("HTTP-Error requesting recordings data: "+err);
      },
      complete:() =>{
        //hide progress bar
        this._session.setEndQuery();
        this.setInfoSubscriber();
      }
        });
  }

  /**
   * Disappear the Info header after 10 seconds
   */
  setInfoSubscriber() :void{
    //vanish InfoMessage if any
    if(this.infoMessage!==undefined){
      const secondsCounter = interval(10*1000);
      this.subscriber = secondsCounter.subscribe(
        n => {
          this.infoMessage = undefined; 
          this.subscriber.unsubscribe();
        }
      );
    }else{
      if(this.subscriber !== undefined){
        this.subscriber.unsubscribe();
      }
    }
  }

  reloadData():void{
    this._session.setShowQuery();
    this.requestRecordingInfo();
  }

  removeInfoMssg(){
    this.infoMessage = undefined;
    this.showError = false;
  }
  removeErrorMssg(){
    this.errorMessage = undefined;
    this.showError = false;
  }

}
