import { Injectable } from '@angular/core';
import { Selector } from '../interfaces/selector';

const heightUnits: Selector[] = [
  {value: 'centimeters', viewValue: 'cm'},
  {value: 'meters', viewValue: 'mt'},
  {value: 'inches', viewValue: 'inch'},
  {value: 'feets', viewValue: 'ft'},
];

const weightUnits: Selector[] = [
  {value: 'kilograms', viewValue: 'Kg'},
  {value: 'grams', viewValue: 'gr'},
  {value: 'pounds', viewValue: 'lbs'},
  {value: 'ounces', viewValue: 'Oz'},
];

@Injectable({
  providedIn: 'root'
})
export class GlobalUtilService {

  hunits = heightUnits;
  wunits = weightUnits;

  constructor() { }
}
