import { Component, EventEmitter, Input, OnChanges, OnInit, Output, SimpleChanges } from '@angular/core';
import { Sensor } from '../../interfaces/sensor';
import { NgbModal, NgbModalConfig, NgbModalRef } from '@ng-bootstrap/ng-bootstrap';
import { FormControl, FormGroup } from '@angular/forms';
import { Observable, map, startWith } from 'rxjs';

@Component({
  selector: 'app-link-recorder-form',
  templateUrl: './link-recorder-form.component.html',
  styleUrls: ['./link-recorder-form.component.css']
})
export class LinkRecorderFormComponent implements OnChanges, OnInit {

  @Input('mode') mode: string ="fab";
  @Input('title') title: string = "Edit Sensor Information";
  @Input('sensor') sensor!: Sensor;
  @Input('filelist') filelist!: string[];
  @Input('error') error!: boolean;

  @Output() submitInfo: EventEmitter<string> = new EventEmitter();
  @Output() submitObject: EventEmitter<Sensor> = new EventEmitter();

  warnMessage: string = "Warning";
  errorEvent:boolean = false;
  modalReference!:NgbModalRef;
  recorderForm!:FormGroup;

  FILE_LIST:string[] = [];
  currentFiles!:Observable<string[]>;
  filesFC:FormControl = new FormControl();

  constructor(config: NgbModalConfig, private modalService: NgbModal,){}

  ngOnInit(): void {

    //init the form
    this.initSensorForm();

    //set listener to the type selector
    this.currentFiles = this.filesFC.valueChanges
          .pipe(
            startWith(''),
            map(value => this._filterFiles(value)));
    //reset the control
    const current = this.recorderForm.get("filepath")?.getRawValue();
    this.filesFC.patchValue(current);
    this.recorderForm.setControl("filepath",this.filesFC);

    
  }

  ngOnChanges(changes: SimpleChanges) {}


  private _filterFiles(value:string){
    const filterValue = value?.toLowerCase();
    return this.filelist.filter(filename => filename.toLowerCase().includes(filterValue));
  }

  setFile(event:any):void{
    console.log(event);
    //this.recorderForm.get("filepath")?.patchValue(event.data);
  }

  initSensorForm(): void{
    if(this.sensor !== undefined && this.sensor.recorder !== undefined){
      this.recorderForm = new FormGroup({
          command: new FormControl(this.sensor.recorder.command),
          filepath: new FormControl(this.sensor.recorder.filepath),
          streamer: new FormControl(this.sensor.recorder.streamer),
          params: new FormControl(this.sensor.recorder.params),
          description: new FormControl(this.sensor.recorder.description)
      });
    }else{

      this.recorderForm = new FormGroup({
        command: new FormControl(''),
        filepath: new FormControl(''),
        streamer: new FormControl('false'),
        params: new FormControl(''),
        description: new FormControl('')
      });

      this.sensor.recorder = this.recorderForm.getRawValue();
    }
  }



  modalOpen(content: any) {
		this.modalReference = this.modalService.open(content, { size:'xl', fullscreen: false, scrollable: true });
	}

  modalClose(){
    this.recorderForm?.reset();
    this.initSensorForm();
    this.modalReference?.close()
    //this.modalService.dismissAll('finished');
  }

  onSubmit(){
    if(!this.recorderForm.invalid){
      let data = this.recorderForm.getRawValue();
      this.sensor.recorder = data
      this.submitObject.next(this.sensor);
    }
  }



}
