import { AfterViewInit, Component, OnInit, ViewChild } from '@angular/core';
import {MatPaginator} from '@angular/material/paginator';
import {MatTableDataSource} from '@angular/material/table';
import { Patient } from '../../interfaces/patient';
import { SessionService } from '../../services/session.service';
import { BackendmapService } from '../../services/backendmap.service';
import { RequestService } from 'src/app/shared/services/request.service';
import { AgentMessage } from '../../interfaces/agent-message';
import { MatSort } from '@angular/material/sort';
import { interval } from 'rxjs';
import { MatSnackBar } from '@angular/material/snack-bar';

@Component({
  selector: 'app-patient-data-table',
  templateUrl: './patient-data-table.component.html',
  styleUrls: ['./patient-data-table.component.css']
})
export class PatientDataTableComponent implements OnInit, AfterViewInit  {

  PATIENT_DATA: Patient[] = [];
  infoMessage: string|undefined;
  errorMessage: string|undefined;
  showError:boolean = false;
  datacount: number = 0;
  subscriber!: any;

  displayedColumns: string[] = ['uuid', 'name', 'alias', 'age', 'height', 'weight','controls'];
  dataSource!: MatTableDataSource<Patient>;

  @ViewChild(MatSort) sort!: MatSort;
  @ViewChild(MatPaginator) paginator!: MatPaginator;

  constructor(private _request: RequestService, public _session:SessionService, 
    private _snackBar: MatSnackBar, private _backend:BackendmapService){

  }

  ngOnInit(): void {
    //Query data
    this._request.sendBackendGet(this._backend.GET_PATIENT_DATA).subscribe({
      next:(res) => {
        let response:AgentMessage = <AgentMessage>JSON.parse(res);
        this.infoMessage =undefined;
        this.errorMessage =undefined;
        if(!response.error){
          this.infoMessage = response.message;
          this.PATIENT_DATA = response.response;
          this.dataSource = new MatTableDataSource<Patient>(this.PATIENT_DATA);
          this.datacount = this.PATIENT_DATA.length;
          if(this.datacount!=0){
            this.dataSource.paginator = this.paginator;
          }
          console.log("info: "+this.infoMessage);
          
        }else{
          this.showError = true;
          this.errorMessage = response.message;
          this.dataSource = new MatTableDataSource<Patient>([]);
          console.log("error: "+this.infoMessage);
        }
      },
      error:(err) => {
        console.error("HTTP-Error requesting patient data: "+err);
      },
      complete:() =>{
        //hide progress bar
        this._session.setEndQuery();
        this.setInfoSubscriber();
      }
        });

  }

  ngAfterViewInit() {
    this.datacount = this.PATIENT_DATA.length;
    if(this.datacount!=0){
      this.dataSource.paginator = this.paginator;
      this.dataSource.sort = this.sort;
    }
  }

  /**
   * Disappear the Info header after 10 seconds
   */
  setInfoSubscriber() :void{
    //vanish InfoMessage if any
    if(this.infoMessage!==undefined){
      const secondsCounter = interval(10*1000);
      this.subscriber = secondsCounter.subscribe(
        n => {
          this.infoMessage = undefined; 
          this.subscriber.unsubscribe();
        }
      );
    }else{
      if(this.subscriber !== undefined){
        this.subscriber.unsubscribe();
      }
    }
  }

  openSnackBar(message: any) {
    console.log(message);
    this._snackBar.open(message, 'dismiss',{
      duration: (7 * 1000),
    });
  }

  reloadData():void{
    this._session.setShowQuery();
    this.ngOnInit();
  }

  onEditEvent(message:string){
    this.openSnackBar(message);
    this.reloadData();
  }

  removeInfoMssg(){
    this.infoMessage = undefined;
    this.showError = false;
  }
  removeErrorMssg(){
    this.errorMessage = undefined;
    this.showError = false;
  }

}
